<template>
     <div class="nearby">
        <h3 class="nearby__title">附近店铺</h3>
        <router-link 
          v-for="item in nearbyList" 
          :key="item.id" 
          :to="`/shop/${item.id}`" 
        >
          <ShopInfo :item="item"/>
        </router-link>
        <!-- <div class="nearby__item"
            v-for='item in nearbyList'
            :key='item._id'
        >
          <img :src="item.imgUrl" class="nearby__item__img">
          <div class="nearby__content">
            <div class="nearby__content__title">{{item.name}}</div>
            <div class="nearby__content__tags"> -->
              <!-- span内容循环 -->
              <!-- <span 
                class="nearby__content__tag"
                v-for="(innerItem, innerIndex) in item.tags"
                :key="innerIndex"
              >{{innerItem}}</span> -->
              <!-- <span class='nearby__content__tag'>月售:{{item.sales}}</span>
              <span class='nearby__content__tag'>起送:{{item.expressLimit}}</span>
              <span class='nearby__content__tag'>基础运费:{{item.expressPrice}}</span>
            </div>
            <p class="nearby__content__highlight">{{item.slogan}}</p>
          </div>
        </div> -->
      </div>
</template>
<script>
import { ref } from 'vue'
import { get } from '../../utils/request'
import ShopInfo from '../../components/ShopInfo'

//店铺展示
const userNearbyListEffcet = ()=>{
  //定义响应数据
  const nearbyList = ref([]);
  //封装获取数据
  const getnearbyList = async ()=>{
    //axios的请求-从utils的get方法里
    const result = await get('/api/shop/hot-list')
    //判断数据库如果errno=0而且有数据
    if (result?.errno === 0 && result?.data?.length){
    //获取数据库数据
      nearbyList.value = result.data
    } 
  }
  return { nearbyList, getnearbyList }
   
}

export default {
    name: 'Nearby',
    components: { ShopInfo },
    setup() {
        //手动写数据
        // const nearbyList = [{
        //     id:1,
        //     imgUrl:'http://www.dell-lee.com/imgs/vue3/near.png',
        //     title:'沃尔玛',
        //     tags:['月售1万+','起送￥0','基础运费$5'],
        //     desc:'VIP尊享满89元减4元运费卷(每月3张)'
        // },{
        //     id:2,
        //     imgUrl:'http://www.dell-lee.com/imgs/vue3/near.png',
        //     title:'沃尔玛',
        //     tags:['月售1万+','起送￥0','基础运费$5'],
        //     desc:'VIP尊享满89元减4元运费卷(每月3张)'
        // }]
        const { nearbyList, getnearbyList } = userNearbyListEffcet();
        getnearbyList();
        return{ nearbyList }
    }
}
</script>
<style lang="scss" scoped> 
